/*-------HEADER------*/
html{width: 100%;height: 100%;}
body, .main_container,nav li a, h1, h2, h3, footer, #mainPageSubTittle, #whiteBoard,a 
{ 
	/* color: #665544;
	background-color: #ffffff/*#1582c29a;*/
	/* font-family: Georgia, 'Times New Roman', Times, serif; */ 
    color: #2e2e2e;
    letter-spacing: .01rem;
    line-height: 1.75rem;
}
body, #root,#mainPage {
    width: inherit;
    height: inherit;
    text-align: center;
}
.main_container, .fix_block{width: inherit; height: inherit;}
#navHeader 
{
    display: inline-flex;
    margin-top: auto;
    margin-left: auto;
    margin-bottom: auto;
    margin-right: auto;
    top: auto;
    bottom: auto;
    background-color: #ffffff;
    align-items: center;
    width: inherit;
    justify-content: space-between;
}

.menu 
{
    /* position: relative;
    left: 70%; */
	overflow: hidden;
	display: inline;
    text-align: right;
	font-size: 100%;
}

.menu ul .navbar 
{
	text-align: right;
	vertical-align: top;
}

.menu li 
{
	float: right;
	margin-right: 2%;
}

.menu li a, #projectsHeading
{
	/*For the links i have added some space 
	between the elements and the borders using the padding
	property. While I use the letter-spacing to have some
	space between the letter*/
	padding-left: .5%;
	padding-right: .5%;
	letter-spacing: 0.2em;
    margin-right: 10px;
    font-family: Montserrat,helvetica neue,Helvetica,Arial,sans-serif;
    font-weight: 700;
	color: #4997D0;
}

.menu li a:hover, .menu li a.current 
{
	color: #f11c1c;
}
